import drag from '@/components/drag/index.vue';
import { login } from '../../api/user.js';
export default {
	data() {
		return {
			isPwd: true,
			showPwd: false,
			password: ''
		};
	},
	components: {
		drag: drag
	},
	methods: {
		changeShowPwd() {
			this.showPwd = !this.showPwd;
		},
		changePwd(e) {
			console.log('1111', e);
		},
		userLogin() {
			let postData = {
				telephone: this.telephone,
				password: this.password
			};
			login(postData).then(
				res => {
					console.log('res' + JSON.stringify(res));
					if (res.errno === 0) {
						uni.redirectTo({
							url:'../index/index'
						})
						uni.setStorageSync('token', res.data.token);
						uni.setStorageSync('userinfo', res.data.userInfo);
						uni.showToast({
						    title: '登录成功',
						    duration: 2000
						});
					}
				},
				err => [console.log('err', err)]
			);
		},
		// 跳转到登录页面
		userRegist() {
			uni.navigateTo({
				url: './regist/index'
			});
		}
	}
};
